.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  flex: 1
}
body {
  background-color: #F3F3F3;
  
}
span {
  font-size: 1.1rem;
  
}
a {
  text-decoration: underline;
  color: blue;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.header {
  display: flex;
  width: 100%;
  flex-direction: row;
  flex: 2;
  transition: all .5s;
  justify-content: center;
  align-items: stretch;
  color: wheat;


}
.header-title {
  flex: 3;
  background-color: #282c34;
}
.header-menu {
  flex: 1;
  background-color: wheat;
 
}
 .header-small-menu{
  transition: all .2s ease-in;
 

}
.header-small-menu > ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
} 

.hide {
  visibility: collapse;
  position: absolute;
  top: 10;
  right: 0;
  
}

li {
  display: inline-block;
  text-decoration: none;
  list-style: none;
  flex: 1;
  margin: 8px 8px 8px 8px;
}

ul {
  display: flex;
  flex-direction: row;
  flex: 1;
  justify-content: center;
  align-items: center;
 
}

menu > ul > li > a, .header-small-menu > ul > li > a { 
  text-decoration: none;
  color: rgb(255, 255, 255);
  font-size: large;
  border-bottom: 1px solid #282c34;
  transition: all .4s ease-in;
 
}
.header-small-menu > ul > li > a {
  color: #282c34;
  display: block;
}
.header-small-menu > ul > li  {
  color: #282c34;
  display: block;
}

menu > ul > li > a:hover { 
  text-decoration: none;
  color: rgb(117, 117, 117);
  font-size: larger;
  padding-bottom: 8px;
  border-bottom: 0px;
 
}


h3 h5 {
  display: inline-block;
}

.image {
  background-color: blue;
  width: 70%;
  height: 100px;
  
}

.menu-small-img {
  width: 0px;
  height: 0px;
  visibility: collapse;
}

.logo-img {
  display: inline-block;
  background-color: #ffffff00;
  margin-left: 8px;
  margin-right: 8px;
  width: 40px;
  height: 40px;
  border-radius: 6;
 
}
.is-sticky {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
  animation:  500ms ease-in-out 0s normal none 1 running ;
  background-color: #ffff !important;
  box-shadow: 0 2px 24px 0 rgba(0, 0, 0, 15%);
}
@media only screen and (max-width: 600px) {
  .header-menu {
      visibility: collapse;
  }
  .small-menu {
    visibility: visible;
    
  }
  .menu-small-img {
    width: 40px;
    height: 40px;
    visibility: visible;
    color: darkgrey;
    align-self: center;
    cursor: pointer;
  }
  .header-small-menu {
    position: absolute;
    top: 30;
    right: 10;
    background-color: #F3F3F3;
  }
}

