.body {
    background-color: white;
    margin-left: 5px;
    margin-right: 5px;
    display: flex;
    flex-direction: column;
    flex:13;
    justify-content: center;
    align-items: center;
  
    
  }
  .about-us {
    flex : 3;
    width: 75%;
   
  }

  .title-txt {
    font-weight: normal;
    font-size: 1.5rem;
  }
  .subtitle-txt {
    font-weight: normal;
    font-size: 1.1rem;
    text-align: start;
    padding-inline-start: 20px;
  }
  .employ {
    margin: 8px 8px;
    padding: 8px;
    padding-bottom: 12px;
    padding-left: 35px;
    box-shadow: 1px 3px 10px lightgray; 
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    overflow:hidden
    
  }
  .emp-img {
    height: 120px;
    width: 120px;
    border-radius: 100px;
    border: .5px dashed grey;
  }
  .info {
    margin-inline-start: 25px;
  }
  .info > p {
    text-align: start;
    color: #282c34;
  }

  .our-work {
    flex: 4;
    background-color: white;
    align-items: center;
    width: 70%;
    
  }
  .logo-img {
    cursor: pointer;
  }
  .qrcode {
      width: 200px;
      height: 200px;
    
      justify-self: flex-end;
  }

  @media only screen and (max-width: 600px) {
    .title-txt {
        font-weight: normal;
        font-size: 1.3rem;
      }
    .subtitle-txt {
        font-weight: normal;
        font-size: 1rem;
        text-align: start;
        padding-inline-start: 20px;
      }
    .logo-img {
        width: 30px;
        height: 30px;
      }
    .emp-img {
        height: 50px;
        width: 50px;
        border-radius: 100px;
        border: .5px dashed grey;
        align-self: flex-start;
      }
      .qrcode {
          visibility: collapse;
          width: 0px;
          height: 0px;
      }
      
  }